
@charset "UTF-8";
/* *********************** */
.header {
  height: 60px;
  background: #fff;
  box-shadow: 0 1px 5px 0 rgba(27, 43, 59, 0.08);
  padding: 10px 0;
}
.header .left-logo {
    line-height: 32px;
    float: left;
    line-height: 40px;
}
.header .left-logo::after {
      clear: both;
      content: "";
      display: table;
}
.header .left-logo img {
      height: 40px;
      display: block;
      float: left;
}
.header .left-logo .title {
      font-size: 24px;
      letter-spacing: 2px;
      font-weight: bold;
      display: inline-block;
      margin-left: 10px;
      color: #6b7277;
}
.header .right-menu {
    float: right;
}
.header .right-menu li {
      padding: 0 10px;
      line-height: 40px;
      float: left;
      font-size: 16px;
      margin-right: 30px;
      color: #242833;
}
.header .right-menu li:last-child {
        margin: 0;
        padding-right: 0;
        color: #e94331;
        font-weight: bold;
        font-size: 18px;
}
.header .right-menu li .mdi {
        font-size: 20px;
}



