
@charset "UTF-8";
/* *********************** */
.item {
  padding: 16px 0;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: stretch;
  background-color: #fff;
  transition: all .3s;
}
.item:hover .title {
    color: #1b90f1 !important;
}
.item.has-line {
    border-bottom: 1px solid rgba(230, 235, 240, 0.8);
}
.item .img-left {
    width: 90px;
    height: 60px;
    display: inline-block;
    margin-right: 10px;
    overflow: hidden;
    border-radius: 3px;
    background-color: #eee;
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.item .img-left.small {
      width: 60px;
      height: 40px;
}
.item .img-left.large {
      width: 120px;
      height: 90px;
      align-self: flex-start;
}
.item .img-left img {
      width: 100%;
      height: 100%;
}
.item .icon-left {
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    text-align: center;
    align-self: center;
}
.item .icon-left .icon {
      font-size: 32px;
      color: #1b90f1;
      line-height: 1;
}
.item .item-body {
    display: flex;
    flex-flow: column nowrap;
    flex: 1;
    line-height: 1.25;
    overflow: hidden;
    position: relative;
}
.item .item-body .item-body-top {
      display: flex;
      justify-content: space-between;
      align-items: center;
}
.item .item-body .item-body-top .title {
        flex: 1;
}
.item .item-body .item-body-top small {
        font-size: 12px;
        color: #999;
}
.item .item-body .item-body-center {
      margin-top: 5px;
      font-size: 12px;
      color: #93999F;
      line-height: 1.6;
      flex: 1;
}
.item .item-body .title {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-size: 14px;
      margin: 0;
      color: #242833;
      transition: all .3s;
}
.item .item-body p {
      width: 100%;
      font-size: 12px;
      color: #93999F;
      margin: 0;
      line-height: 1.6;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
}
.item .item-body .item-body-footer {
      margin-top: 5px;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      font-size: 12px;
      color: #93999F;
}
.item .item-body .item-body-footer span + span {
        margin-left: 10px;
}
.item .item-body .item-body-footer .left {
        margin-right: 10px;
}
.item .item-body .item-body-footer .center {
        flex: 1;
}
.item .item-body .item-body-footer .right {
        margin-left: 10px;
}
.item .item-right {
    margin-left: 10px;
    align-self: center;
}
.item.avatar-left {
    padding-top: 12px;
    padding-bottom: 12px;
}
.item.avatar-left .img-left {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      align-self: flex-start;
}
.item.c-item {
    border-radius: 5px;
}
.item.c-item .img-left {
      width: 80px;
      height: 80px;
      margin-right: 20px;
}
.item.card-style {
    padding: 16px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 1px 5px 0 rgba(27, 43, 59, 0.08);
    margin-bottom: 16px;
}
.item.border-style {
    padding: 16px;
    border: 1px solid #e6ebf0;
    border-radius: 2px;
    margin-bottom: 16px;
}



