
@charset "UTF-8";
/* *********************** */
.g-item {
  border-radius: 5px;
  padding-bottom: 10px;
  margin-bottom: 10px;
  display: block;
}
.g-item .media {
    transition: all .3s;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    display: block;
    background-color: #eee;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 200px;
}
.g-item .media img {
      width: 100%;
      display: block;
      transition: all .3s;
}
.g-item .media .state {
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 5px 0 5px 0;
      background: rgba(233, 67, 49, 0.8);
      font-size: 14px;
      padding: 6px 15px;
      color: #fff;
}
.g-item .media .state.bg-red {
        background: rgba(233, 67, 49, 0.8);
}
.g-item .author {
    position: relative;
    padding: 5px 10px 10px 70px;
    color: #93999F;
    font-size: 12px;
    display: block;
}
.g-item .author .avatar-box {
      border: 2px solid #fff;
      position: absolute;
      left: 10px;
      top: -20px;
}
.g-item .title {
    text-align: center;
    display: block;
    font-size: 14px;
    color: #242833;
    font-weight: normal;
    line-height: 1.6;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 8px 0;
}
.g-item .bottom {
    display: flex;
    align-items: center;
    font-size: 12px;
    color: #93999F;
}
.g-item .bottom .price {
      font-size: 14px;
}
.g-item:hover .media {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transform: translateY(-1px);
}
.g-item:hover .media img {
      transform: scale(1.2) rotate(5deg);
}
.g-item:hover .title {
    color: #e94331;
}
.g-item.img-style .media {
    background: none !important;
    height: auto !important;
}



