
@charset "UTF-8";
/* *********************** */
.list .list-footer {
  background: #fff;
  position: relative;
  color: #6b7277;
  font-size: 14px;
  text-align: center;
  padding: 10px;
  position: relative;
}
.list .list-footer:before, .list .list-footer:after {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    transform: scaleY(0.5);
    height: 1px;
    left: 0;
    right: 0;
}
.list .list-footer:before {
    top: 0;
    transform-origin: top;
}
.list .list-footer:after {
    bottom: 0;
    transform-origin: bottom;
}



