.line-top {
  position: relative;
}
.line-top:before {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    z-index: 9;
    height: 1px;
    left: 0;
    right: 0;
    top: 0;
    transform: scaleY(0.5);
    transform-origin: top;
}
.line-left {
  position: relative;
}
.line-left:before {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    z-index: 9;
    width: 1px;
    left: 0;
    top: 0;
    bottom: 0;
    transform: scaleX(0.5);
    transform-origin: left;
}
.line-right {
  position: relative;
}
.line-right:before {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    z-index: 9;
    width: 1px;
    top: 0;
    right: 0;
    bottom: 0;
    transform: scaleX(0.5);
    transform-origin: right;
}
.line-bottom {
  position: relative;
}
.line-bottom:before {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    z-index: 9;
    height: 1px;
    left: 0;
    right: 0;
    bottom: 0;
    transform: scaleY(0.5);
    transform-origin: bottom;
}
.line-tb {
  position: relative;
}
.line-tb:before, .line-tb:after {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    transform: scaleY(0.5);
    height: 1px;
    left: 0;
    right: 0;
}
.line-tb:before {
    top: 0;
    transform-origin: top;
}
.line-tb:after {
    bottom: 0;
    transform-origin: bottom;
}
.line-lr {
  position: relative;
}
.line-lr:before, .line-lr:after {
    content: "";
    position: absolute;
    background-color: #e6ebf0;
    transform: scaleX(0.5);
    width: 1px;
    top: 0;
    bottom: 0;
}
.line-lr:before {
    left: 0;
}
.line-lr:after {
    right: 0;
}
