a {
  text-decoration: none;
}
h4.title {
  font-size: 14px;
  font-weight: bold;
  border-left: 4px solid #1b90f1;
  line-height: 1;
  padding: 0 0 0 10px;
  margin: 16px 0;
  color: #242833;
  position: relative;
}
.center-title {
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 600;
  letter-spacing: 6px;
  color: #6b7277;
  text-align: center;
  line-height: 36px;
  position: relative;
}
.center-title:before, .center-title:after {
    content: "";
    height: 36px;
    width: 56px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-image: url("/static/icons/icon.png");
    display: inline-block;
    vertical-align: -30%;
}
.center-title:before {
    background-position: center -216px;
    margin-right: 20px;
}
.center-title:after {
    background-position: center -252px;
    margin-left: 10px;
}
.center-title .more {
    position: absolute;
    right: 0;
    top: 50%;
    -webkit-transform: translate(0, -50%);
    -moz-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    -o-transform: translate(0, -50%);
    transform: translate(0, -50%);
    font-size: 12px;
    color: #93999F;
    font-weight: normal;
    letter-spacing: 1px;
}
.center-title .more:after {
      content: "";
      display: inline-block;
      width: 6px;
      height: 6px;
      border: solid #bfc0c5;
      border-width: 2px 2px 0 0;
      margin-left: 5px;
      transform: rotate(45deg) scale(0.8);
}
.center-title .more:hover {
      color: #1b90f1;
}
.center-title .more:hover:after {
        border-color: #1b90f1;
}
.deep-bg .center-title {
    color: rgba(255, 255, 255, 0.8);
}
.deep-bg .center-title:before {
      background-position: center -504px;
}
.deep-bg .center-title:after {
      background-position: center -540px;
}
.deep-bg .center-title .more {
      color: rgba(255, 255, 255, 0.8);
}
.deep-bg .center-title .more:after {
        border-color: rgba(255, 255, 255, 0.6);
}
.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  display: inline-block;
  overflow: hidden;
  border: 2px solid #fff;
}
.avatar img {
    width: 100%;
    height: 100%;
    display: block;
}
.avatar.large {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.icons {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.icons .icon {
    line-height: 1;
    color: #242833;
    font-size: 20px;
}
.icons .icon + .icon {
      margin-left: 15px;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.swiper-container.has-mask:after, .swiper-container.has-mask:before {
  content: "";
  position: absolute;
  background: #fff;
  width: 40px;
  top: 0;
  bottom: 0;
  z-index: 8;
}
.swiper-container.has-mask:after {
  right: 0;
}
.swiper-container.has-mask:before {
  left: 0;
}
.swiper-container .swiper-wrapper .swiper-slide {
  background-position: center;
  background-size: cover;
  transition-duration: 1s !important;
}
.swiper-container .swiper-wrapper .swiper-slide img {
    display: block;
    width: 100%;
}
.swiper-container .swiper-pagination {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 10px;
}
.swiper-container .swiper-pagination .swiper-pagination-bullet {
    width: 8px;
    height: 8px;
    margin: 0 5px;
    transition: all .3s;
}
.swiper-container .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
      width: 16px;
      border-radius: 10px;
      background: #1b90f1;
}
.swiper-container [class^="arrow-"] {
  position: absolute;
  top: 50%;
  font-size: 24px;
  line-height: 24px;
  margin-top: -12px;
  color: #93999F;
  opacity: .8;
  z-index: 9;
}
.swiper-container [class^="arrow-"].swiper-button-disabled {
    opacity: .3;
}
.swiper-container .arrow-prev {
  left: 10px;
}
.swiper-container .arrow-next {
  right: 10px;
}
.card-item {
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 4px 8px 0 rgba(7, 17, 27, 0.05);
  text-align: center;
  margin-bottom: 10px;
  display: block;
  position: relative;
  margin-top: 48px;
  transition: all .3s;
  color: #6b7277;
}
.card-item .media {
    height: 240px;
}
.card-item .title {
    padding-top: 10px;
    font-size: 18px;
    color: #242833;
    line-height: 1.6;
}
.card-item .label {
    font-size: 12px;
    line-height: 1.6;
    letter-spacing: 2px;
    text-transform: uppercase;
}
.card-item .text-block {
    padding: 10px 30px 20px;
    line-height: 2;
    font-size: 14px;
    text-align: left;
    color: #6b7277;
}
.card-item:hover {
    background-color: #fff;
    transform: scale(0.95);
}
.icon-text {
  color: #93999F;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.icon-text .icon {
    font-size: 18px;
    color: inherit;
    margin-right: 5px;
    line-height: 1;
    text-align: center;
}
.icon-text .text {
    font-size: 12px;
    color: inherit;
    line-height: 18px;
    text-align: center;
}
ul.number-info {
  display: flex;
  align-items: center;
  justify-content: center;
}
ul.number-info li {
    padding: 0 24px;
    position: relative;
    text-align: center;
}
ul.number-info li .number {
      font-size: 24px;
      font-weight: 600;
}
ul.number-info li .label {
      font-size: 12px;
      margin-top: 10px;
}
ul.number-info li + li:before {
      content: "";
      width: 1px;
      position: absolute;
      left: 0;
      top: 10px;
      bottom: 10px;
      background-color: #e6ebf0;
}
.share-action {
  display: flex;
}
.share-action .share {
    width: 42px;
    height: 42px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.3);
    transition: all  .3s;
}
.share-action .share .icon {
      color: rgba(255, 255, 255, 0.8);
      font-size: 24px;
}
.share-action .share + .share {
      margin-left: 20px;
}
.share-action .share:hover {
      background-color: #1b90f1;
      box-shadow: 0 0 0 2px #1b90f1;
}
.icon-tag {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 10px;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
}
.icon-tag .icon {
    font-size: 16px;
    margin-right: 5px;
}
.icon-tag .text {
    font-size: 14px;
}
html, body, .wrapper {
  width: 100%;
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}
body {
  font-family: "PingFang SC", "microsoft yahei", arial, STHeiTi, sans-serif;
  font-size: 14px;
}
.header {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 99;
}
.header + .page {
    padding-top: 60px;
}
.main-content {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 60px;
}
.container {
  box-sizing: border-box;
  padding-left: 16px;
  padding-right: 16px;
}
.container::after {
    clear: both;
    content: "";
    display: table;
}
@media screen and (min-width: 1281px) {
.container {
      width: 1200px;
      margin: 0 auto;
}
}
.main .content {
  background: #fff;
  min-height: 200px;
}
.white-block {
  background: #fff;
  box-shadow: 0 1px 5px 0 rgba(27, 43, 59, 0.08);
  padding: 40px 0;
}
.section {
  padding: 40px 0;
