
@charset "UTF-8";
/* *********************** */
.footer {
  background-color: #2C313E;
  color: #969CAC;
  padding: 20px 0;
}
.footer a {
    color: inherit;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    transition: 0.3s;
    font-size: 14px;
}
.footer a:hover {
      color: #1b90f1;
}
.footer .links-wrap {
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.footer .group-title {
    font-size: 18px;
    color: #787E8F;
    margin-bottom: 15px;
    font-weight: 600;
}
.footer .link-list li {
    padding: 5px 0;
}
.footer .link-list li a {
      display: block;
      line-height: 2;
}
.footer .link-list.inline::after {
    clear: both;
    content: "";
    display: table;
}
.footer .link-list.inline li {
    float: left;
    margin-right: 15px;
}
.footer .copyright {
    padding: 20px 0 0;
    text-align: center;
    line-height: 2;
    font-size: 12px;
    color: #6c7489;
}
.footer.fixed-bottom {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
}



